.home{
      height: 93vh;
      width: 100%;

      header{
            display: flex;
            align-items: center;
            gap: 10rem;
            padding: 0 0rem 0 8rem;
            width: 100%;
            height: 10%;
            font-weight: 600;
            background-color: var( --lightGrey);
            box-shadow: 0 2px 10px 0 rgba(0,0,0,0.1);
            border-bottom: 1px solid rgba(211, 211, 211, 0.389);

           .header-logo{
            text-decoration: none;
            color: var(--copyColor);
             
                  h1{
                        font-family: 700;
                        font-size: 2rem;
                  }
                  &:hover{
                        color: rgb(12, 12, 108);
                  }
                  &:active{
                        transform: translateY(2px);
                  }
           }

           .menu{
            display: none;
           }

          
            nav{
                  display: flex;
                  height: 100%;
                  padding-top: 1.7rem;
                  gap: 6rem;
                  font-size: 1.2rem;
                        
                        a{
                              text-decoration: none;
                              color: var(--copyColor);

                              &:hover{
                                    color: var(--primaryColor);
                              }
                        }

                        .active{
                              border-bottom: 2.5px solid var(--primaryColor);
                        }
                  }

                  .user{
                        display: flex;
                        gap: 1.5rem;
                        margin-left: 16rem;
                        align-items: center;

                        &-line{
                              height: 5rem;
                              width: 1px;
                        background-color: var(--borderColor);
                        }

                        img{
                              height: 3.5rem;
                              width: 3.5rem;
                              object-fit: cover;
                              border-radius: 50%;
                        }
                        
                        p{
                              font-size: 1.1rem;
                              margin-bottom: 0.2rem;
                        }

                        a{
                              text-decoration: none;
                              font-weight: 700;

                              &:hover{
                                    color: var(--primaryColor)
                        }
                  }
            }
            
      }
      main{
            width: 100%;
            height: 90%;
      }
}

@media screen and (max-width: 600px) {
      .home{

      header{
            display: block;
            align-items: center;
            gap: 0;
            padding: 0 4rem;
            height: 8%;
            
            .header-logo{
            display: flex;
            align-items: center;
            height: 100%;
            width: 0;
           }

           .menu{
            display: block;
            position: absolute;
            top: 2rem;
            right: 5rem;
            cursor: pointer;
           }

            .nav{
                  display: none
            }

            .user{
                  display: none;
            }
           
           .side-bar{
            position: absolute;
            background-color: white;
            top: 0;
            left: 0;
            width: 25rem;
            height: 100vh;
            padding: 2rem;
            animation: slideIn 0.6s ease forwards alternate;

            @keyframes slideIn {
                  0%{transform: translateX(-100%);}
                  100%{transform: translateX(0);}
            }

            nav{
                  display: flex;
                  flex-direction: column;
                  height: 0;
                  padding-top: 1rem 0;
                  gap: 0;
                  font-size: 1.2rem;
                  
                  a{
                        text-decoration: none;
                        color: var(--copyColor);

                        &:hover{
                              color: var(--primaryColor);
                        }
                  }

                  .active{
                        border-bottom: 2.5px solid var(--primaryColor);
                  }
            }
 
            .user1{
                  display: flex;
                  flex-direction: column;
                  gap: 1.5rem;
                  margin-left: 0;
                  align-items: flex-start;

                  &-line{
                        height: 5rem;
                        width: 1px;
                       background-color: var(--borderColor);
                  }

                  img{
                        height: 3.5rem;
                        width: 3.5rem;
                        object-fit: cover;
                        border-radius: 50%;
                  }
                  
                  p{
                        font-size: 1.1rem;
                        margin-bottom: 0.2rem;
                  }

                  a{
                        text-decoration: none;
                        font-weight: 700;

                        &:hover{
                              color: var(--primaryColor)
                        }
                  }
            }
            }
      }
            

      main{
            width: 100%;
            height: 90%;
      }
}
}